import { Button, TextField, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { useContext } from "react";
import { appContext } from "../../context";
import AlertPopup from "../UI/Popup/AlertPopup";

const AddPage = () => {

    const {userIsLogin} = useContext(appContext);

    return (
        <Box component="main">
            <Box sx={{ mb: 3 }} className="title-page">
                <Typography variant="h4" className="note">
                    Добавить точку
                </Typography>
            </Box>
            <AlertPopup props={"mb-2"} />
            <Box className="flex-column gap-1 paper wfull addForm" sx={{ px: 4, py: 2 }}>
                {userIsLogin
                    ?   <>
                            <Box className="flex-column gap-0">
                                <Typography variant="h3">
                                    Название
                                </Typography>
                                <TextField label="Максимум 32 символа" variant="outlined" />
                            </Box>
                            <Box className="flex-column gap-0">
                                <Typography variant="h3">
                                    Описание
                                </Typography>
                                <TextField label="Максимум 255 символов" variant="outlined" multiline />
                            </Box>
                            <Box className="flex-column gap-0">
                                <Box className="flex-row wrap">
                                    <Typography variant="h3">
                                        Расположение&nbsp;
                                    </Typography>
                                    <Typography variant="h3" className="note">
                                        метро или город
                                    </Typography>
                                </Box>
                                <TextField label="Максимум 128 символов" variant="outlined" multiline />
                            </Box>
                            <Box className="flex-column gap-0">
                                <Box className="flex-row wrap">
                                    <Typography variant="h3">
                                        Цена&nbsp;
                                    </Typography>
                                    <Typography variant="h3" className="note">
                                        в рублях
                                    </Typography>
                                </Box>
                                <TextField label="Максимум 16 символов" variant="outlined" multiline />
                            </Box>
                        </>
                    :   <Typography variant="h4">
                            Чтобы добавить свою точку надо войти на сайт!
                        </Typography>
                }
            </Box>
            {userIsLogin
                ?   <Box className="error-page">
                        <Button 
                            variant="contained" 
                            disableElevation
                            sx={{ mt: 2 }}
                        >
                            Добавить
                        </Button>
                    </Box>
                :   <></>
            }
        </Box>
    )
}

export default AddPage;