import { Typography } from "@mui/material";
import { Box } from "@mui/system";
import React from "react";
// import ReturnBackButton from "../UI/Button/ReturnBackButton";

const AboutPage = () => {

    return (
        <Box component="main" className="aboutPage">
            <Box sx={{ mb: 3 }} className="title-page">
                <Typography variant="h4" className="note">
                    О проекте
                </Typography>
            </Box>
            {/* <ReturnBackButton /> */}
            <Box className="flex-column gap-3 paper" sx={{ px: 4, py: 2, mt: 3 }}>
                <Typography variant="h5">
                    Идея
                </Typography>
                <Typography variant="h6">
                    Идея возникла спонтанно, когда после пар пару студентов очень сильно хотели есть... Правда в особенности проект так и не получил продолжения из-за недостатка точек поблизости, однако(!!!) по-тихоньку точки добавляем, а чтобы стало проще я решил создать этот сайт
                </Typography>
            </Box>
            <Box className="flex-column gap-3 paper" sx={{ px: 4, py: 2, mt: 3 }}>
                <Typography variant="h5">
                    Сайт
                </Typography>
                <Typography variant="h6">
                    На данный момент сайт находится в стадии ранней разработке и имеет неполный функционал, все будет, но не сразу
                </Typography>
            </Box>
            <Box className="flex-column gap-3 paper" sx={{ px: 4, py: 2, mt: 3 }}>
                <Box className="flex-row gap-1">
                    <Typography variant="h5">
                        Разработчик
                    </Typography>
                    <Typography variant="h5" className="note">
                        Дизайн + код
                    </Typography>
                </Box>
                <Typography variant="h6">
                    Интересный факт: на написания данного сайта потрачено 15 часов и это только начало
                </Typography>
                <Typography variant="h4">
                    <a href="https://github.com/roso1nik" target="_blank" rel="noreferrer noopener">
                        github@roso1nik
                    </a>
                </Typography>
            </Box>
        </Box>
    )
}

export default AboutPage;